.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}

.dialogItems{
  padding: 10px;
}

.diolog {
  display: flex;
  border-right: 1px solid black;
}

.dialogItems img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
  margin-bottom: 25px;
}

.dialogItems a {
 color: black;
 text-decoration: none;
 margin-top: 13px;
 font-size: 20px;
}

.dialogItems .active {
  color: rgba(10, 164, 230, 0.793);
}

.messages {
  display: flex;
  flex-direction: column;
}

.message {
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 30px;
}

.messages img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 100px;
  margin-top: 5px;
}

.messageContent {
  width: 50vh;
  margin-top: 5px;
  font-size: 20px;
  padding: 10px;
  color: black;
  border-radius:5px;
  border: 1px solid rgb(138, 131, 131);
}